*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}a,a:hover,a:visited{text-decoration:none}li{list-style:none}h1,h2,h3{font-family:"Josefin Sans", sans-serif;font-weight:300}p{font-family:"Alata", sans-serif}:root{font-family:"Roboto",sans-serif;color:#06111f}header li{font-family:"Pacifico",cursive;font-size:3rem;font-style:italic;color:black}.nav-container{background-color:white;opacity:none}.navbar{display:flex;display:inline-block;width:100vw;height:10vh;background-color:transparent;border-left:30px solid white;border-right:30px solid white}nav li{list-style:none;color:black;margin:0.5rem;font-size:1rem}nav li li:hover{color:#303030;transition:0.3s;padding:-10px}#logo-wrapper .logo img{margin:0px;width:120px;height:18px}.search-bar{border:1px solid #eb5645;border-radius:50px;height:25px;width:40vw;font-size:12px;padding:10px}.search-bar:focus{outline:none}footer{background-color:aliceblue}*{font-family:"Montserrat",sans-serif}

